/** @jsxImportSource @emotion/react */
import { useCallback, useEffect, useRef, useState } from "react";
import TextField from "@mui/material/TextField";
import axios from "axios";
import { CHAT_API, SPEECH2TEXT } from "../../utils/Constants";
import ChatBubble from "../../components/chatbubble/ChatBubble";
import BouncingDotsLoader from "../../components/assets/animation/BouncingDotsLoader";
import { useSearchParams } from "react-router-dom";
import { isEnterKey } from "../../utils/FormHelper";
import { useAudioRecorder } from "react-audio-voice-recorder";
import IconButton from "@mui/material/IconButton";
import { Mic as MicIcon } from "@mui/icons-material";
import SendIcon from "@mui/icons-material/Send";
import { blobToBase64 } from "../../utils/Formatter";
import { ChatroomStyles } from "./styles";
import { useLongPress } from "../../utils/hooks/useLongPress";
import SoundWaves from "../../components/assets/animation/SoundWaves";
import ChatroomHeader from "../../components/chatroomheader/ChatroomHeader";
import ScreenContainer from "../../components/screencontainer/ScreenContainer";
import SuggestTile from "../../components/suggesttile/SuggestTile";

function Chatroom() {
  const recorderControls = useAudioRecorder();
  const [searchParams] = useSearchParams();
  const username = searchParams.get("name");
  const mode = searchParams.get("mode");

  const [userInput, setUserInput] = useState<string>("");
  const [isRecording, setIsRecording] = useState<boolean>(false);
  const [loading, setLoading] = useState<"user" | "assistant" | undefined>(
    undefined,
  );
  const [conversations, setConversations] = useState<
    { role: "assistant" | "user"; content: string }[]
  >([
    {
      role: "assistant",
      content: `Hi ${username}! Welcome to the chatroom! Ask me a question!`,
    },
  ]);

  const resetUserInput = () => {
    setUserInput("");
  };

  const getChatResponse = (input: string) => {
    setConversations((prev) => [...prev, { role: "user", content: input }]);
    resetUserInput();
    setLoading("assistant");
    axios
      .get(CHAT_API, {
        headers: { Accept: "application/json" },
        params: {
          user: input,
          mode: mode,
          prevMsgs: JSON.stringify(conversations),
        },
      })
      .then((res) => {
        resetUserInput();
        setConversations((prev) => [
          ...prev,
          { role: "assistant", content: res.data },
        ]);
      })
      .catch((err) => {
        resetUserInput();
      })
      .then(() => setLoading(undefined));
  };

  const getSpeechToText = useCallback((base64: string) => {
    setLoading("user");
    axios
      .post(`${CHAT_API}${SPEECH2TEXT}`, {
        data: JSON.stringify(base64),
      })
      .then((res) => {
        resetUserInput();
        getChatResponse(res.data);
      })
      .catch((err) => {
        resetUserInput();
        setLoading(undefined);
      });
  }, []);

  const startRecording = () => {
    setIsRecording(true);
    recorderControls.startRecording();
  };

  const stopRecording = () => {
    setIsRecording(false);
    recorderControls.stopRecording();
  };

  const longPress = useLongPress(startRecording, stopRecording);

  const onKeyDown = (evt: React.KeyboardEvent<HTMLDivElement>) => {
    if (!isEnterKey(evt)) {
      return;
    }
    getChatResponse(userInput);
  };

  useEffect(() => {
    if (!recorderControls.recordingBlob) return;

    // turn blob to baseB4 then send to api
    blobToBase64(recorderControls.recordingBlob, getSpeechToText);

    // recordingBlob will be present at this point after 'stopRecording' has been called
  }, [getSpeechToText, recorderControls.recordingBlob]);

  const AlwaysScrollToBottom = () => {
    const elementRef = useRef<null | HTMLDivElement>(null);
    useEffect(() => elementRef.current?.scrollIntoView());
    return <div ref={elementRef} />;
  };

  return (
    <main css={ChatroomStyles.chatroomScreen}>
      <ScreenContainer>
        <ChatroomHeader />
        <div css={ChatroomStyles.chatroomParentContainer}>
          <div css={ChatroomStyles.chatbubbleParentContainer}>
            {conversations.map((convo, id) => (
              <ChatBubble
                key={id}
                role={convo.role}
                css={ChatroomStyles[convo.role]}
              >
                {convo.content}
              </ChatBubble>
            ))}
            {loading && (
              <ChatBubble role={loading}>
                <BouncingDotsLoader />
              </ChatBubble>
            )}
            <AlwaysScrollToBottom />
          </div>
          <div css={ChatroomStyles.inputParentContainer}>
            <div css={ChatroomStyles.suggestionTilesContainer}>
              <SuggestTile
                onClick={(t) => getChatResponse(t)}
                text="Are you free for a chat from 1pm-1:30pm tomorrow?"
              />
              <SuggestTile
                onClick={(t) => getChatResponse(t)}
                text="Tell me about your experience in AI."
              />
              <SuggestTile
                onClick={(t) => getChatResponse(t)}
                text="Tell me about your most recent role."
              />
            </div>
            <div css={ChatroomStyles.inputContainer}>
              {!isRecording && (
                <TextField
                  multiline
                  fullWidth
                  disabled={loading ? true : false}
                  label="Ask me a question"
                  variant="outlined"
                  value={userInput}
                  onChange={(evt) =>
                    setUserInput(evt.target.value.replace("\n", ""))
                  }
                  onKeyDown={onKeyDown}
                />
              )}
              {isRecording && (
                <div css={ChatroomStyles.soundBar}>
                  <SoundWaves />
                </div>
              )}
              <IconButton
                css={[
                  ChatroomStyles.micButton,
                  isRecording && ChatroomStyles.micButtonFlash,
                ]}
                {...longPress}
                size="large"
              >
                {userInput ? <SendIcon /> : <MicIcon />}
              </IconButton>
            </div>
          </div>
        </div>
      </ScreenContainer>
    </main>
  );
}

export default Chatroom;
