/** @jsxImportSource @emotion/react */
import { ChatBubbleProps } from "./type";
import { ChatBubbleStyles } from "./styles";
import {
  ASSISTANT_PROFILE_URL,
  USER_PROFILE_URLS,
} from "../../utils/Constants";

const profileUrls = {
  assistant: ASSISTANT_PROFILE_URL,
  user: USER_PROFILE_URLS[
    Math.floor(Math.random() * (USER_PROFILE_URLS.length - 1))
  ],
};

function ChatBubble({ role, children, ...props }: ChatBubbleProps) {
  return (
    <div
      css={[
        ChatBubbleStyles.chatBubbleContainer,
        ChatBubbleStyles.bubbleContainer[role],
      ]}
      {...props}
    >
      <img
        src={profileUrls[role]}
        css={ChatBubbleStyles.profileImg}
        alt="avatar"
      />
      <div css={[ChatBubbleStyles.bubble, ChatBubbleStyles[role]]}>
        {children}
      </div>
    </div>
  );
}

export default ChatBubble;
