function SuggestTile({
  text,
  onClick,
}: {
  text: string;
  onClick: (s: string) => void;
}) {
  return (
    <div
      onClick={() => onClick(text)}
      style={{
        cursor: "pointer",
        background: "#282a3b",
        padding: "0.8rem",
        borderRadius: "1rem",
        opacity: "0.7",
        border: "1px solid rgba(245, 78, 162, 0.8)",
        whiteSpace: "nowrap",
      }}
    >
      {text}
    </div>
  );
}

export default SuggestTile;
