export const blobToBase64 = (blob: any, callback: (base64: string) => void) => {
  const reader = new FileReader();
  reader.onload = () => {
    const dataUrl = reader.result;
    if (!dataUrl) {
      return;
    }
    const base64 = (dataUrl as string).split(",")[1];
    callback(base64);
  };
  reader.readAsDataURL(blob);
};
