/** @jsxImportSource @emotion/react */

import { ArticleBlockProps } from "./types";
import { ArticleBlockStyles } from "./styles";
import { GlobalStyles } from "../../global/GlobalStyles";

function ArticleBlock(props: ArticleBlockProps) {
  const onRedirectClick = () => {
    window.location.href = props.redirectUrl;
  };

  return (
    <div css={ArticleBlockStyles.parentContainer} onClick={onRedirectClick}>
      <div css={ArticleBlockStyles.imgContainer}>
        <img width="100%" alt="cover" src={props.imgUrl} />
      </div>
      <div css={ArticleBlockStyles.contentContainer}>
        <h3>{props.title}</h3>
        <p css={GlobalStyles.subText}>{props.desc}</p>
      </div>
    </div>
  );
}

export default ArticleBlock;
