/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import TextField from "@mui/material/TextField";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Button from "@mui/material/Button";

const HomeStyles = {
  screen: css({
    minHeight: "100vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    textAlign: "center",
    padding: "1rem",
  }),
  buttonContainer: css({
    display: "flex",
    flexDirection: "row",
    gap: "1rem",
  }),
};

function DevScreen() {
  const navigate = useNavigate();
  const [userInput, setUserInput] = useState<string>("");

  const onChatClick = (mode: "casual" | "formal") => {
    navigate(`/chat?name=${userInput}&mode=${mode}`);
  };

  return (
    <main css={HomeStyles.screen}>
      <h1>Welcome to Susum's chatroom</h1>
      <h2>Enter your name to get started</h2>
      <TextField
        label="Enter your name"
        variant="outlined"
        onChange={(evt) => setUserInput(evt.target.value)}
      />
      {userInput.length > 0 && (
        <>
          <h3>Am I close to you? 🤔</h3>
          <div css={HomeStyles.buttonContainer}>
            <Button
              size="large"
              variant="contained"
              onClick={() => onChatClick("casual")}
            >
              Yes 🤗
            </Button>
            <Button
              size="large"
              variant="contained"
              onClick={() => onChatClick("formal")}
            >
              No 🙃
            </Button>
          </div>
        </>
      )}
    </main>
  );
}

export default DevScreen;
